html {
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background: none;
  font-size: 16px; }

.header {
  height: 9rem;
  width: 100%;
  padding: 0.5rem 1rem;
  align-items: center; }
  .header__a {
    font-family: "Open San", sans-serif;
    font-weight: 500;
    line-height: 25px;
    font-size: 1rem;
    color: #0f3074;
    text-decoration: none; }
  .header__cont {
    margin: auto auto;
    max-width: 1200px;
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
    .header__cont--logo {
      display: flex; }
      .header__cont--logo-png {
        width: 195px;
        height: 125px; }
    .header__cont--mail {
      display: flex;
      padding: 1rem 1.9rem; }
      .header__cont--mail-email {
        margin: 0 50px 0 0; }

.phoneSvg {
  fill: blue;
  width: 1rem; }

.st0 {
  height: 10px;
  width: 10px;
  fill: red; }

.navigation {
  background-color: #2d3e50;
  display: flex;
  justify-content: center;
  position: sticky;
  top: 0;
  width: 100%; }
  .navigation__cont {
    width: 1200px; }
    .navigation__cont--contUl {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      padding: 0;
      margin: 0;
      flex-direction: row; }
    .navigation__cont--item {
      list-style-type: none;
      margin: 1rem .6rem; }
      .navigation__cont--item--a {
        text-decoration: none;
        color: white;
        text-transform: uppercase;
        font-size: 14px;
        padding: 15px; }
        .navigation__cont--item--a:hover {
          color: #19adee; }

.picture {
  height: 100%;
  width: 100%; }
  .picture__img {
    width: 100%;
    height: 100%; }

.main {
  display: flex;
  justify-content: center; }
  .main__contener {
    width: 1000px;
    margin: 10px, auto; }
    .main__contener--offerHeader {
      color: #2d3e50;
      font-family: Roboto;
      text-transform: uppercase;
      font-size: 2rem;
      font-weight: 600;
      letter-spacing: 1.5px;
      text-align: center;
      padding: 2.5rem 0 4rem 0;
      border-bottom: solid 1.5px #19adee;
      margin-bottom: 2rem; }
    .main__contener--svgLink {
      display: block; }
      .main__contener--svgLink-svgBox {
        color: #19adee;
        font-family: Roboto;
        text-transform: uppercase;
        text-align: center;
        width: 15rem;
        font-weight: 600;
        line-height: 1.25rem;
        font-size: 1.25rem;
        padding: 0.5rem 0; }

.contener__first {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  min-height: 10rem;
  border-bottom: solid 1px #19adee;
  margin: 1.5rem 0 2rem; }

.contener__second {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  min-height: 10rem;
  border-bottom: solid 1px #19adee;
  margin: 1.5rem 0 2rem; }

.main__list {
  list-style-type: circle;
  text-align: left; }
  .main__list--item {
    color: #2d3e50;
    font-family: 'Open Sans', sans-serif;
    text-transform: lowercase;
    font-weight: 200;
    font-size: .9rem; }

.footer {
  background-color: #19adee; }
  .footer__cont {
    text-align: center;
    margin: 0 auto;
    padding: 2rem 0 1rem 0;
    max-width: 1200px; }
    .footer__cont--1 {
      font-family: 'Roboto', sans-serif;
      text-transform: uppercase;
      font-size: 36px;
      font-weight: 600;
      letter-spacing: 1.3px;
      color: white; }
    .footer__cont--2 {
      font-family: 'Roboto', sans-serif;
      font-size: 1rem;
      font-weight: 400;
      letter-spacing: 1.3px;
      color: white; }

.break {
  min-height: 80px;
  background-color: white; }

.bootom {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 15px;
  height: 40px;
  width: 100%;
  background-color: #19adee;
  color: white; }
  .bootom__1 {
    padding: 10px 15px;
    font-size: 12px; }
  .bootom__2 {
    padding: 10px 15px;
    font-size: 12px; }
